:1px solid rgba(0,0,0,0.26);border-radius:4px}.main-wrapper .form-control{margin-bottom:30px;width:100%}.main-wrapper .summary-header,.main-wrapper .roll-up-header{font-weight:700;background-color:#e3e3e3;padding-left:10px;padding-right:10px;margin-left:-10px;margin-right:-10px;width:calc(100% + 20px)}.main-wrapper .roll-up-header{font-weight:600;background-color:#efefef}.not-found{width:100%;height:550px;background:url(/optimiser/static/media/404-Error.507c0ddc.png) no-repeat center}.result-container{max-width:300px;padding:10px;margin:10px auto}.button-container{display:flex;justify-content:center;align-items:center;justify-content:space-evenly}.image-container{display:flex;justify-content:center;align-items:center;padding:15px;height:250px;width:40%;margin:auto;margin-bottom:10px}@media (max-width: 768px){.image-container{width:100%}}.image-container img{max-width:100%;max-height:100%}.image-container .upload-container{display:flex;justify-content:center;align-items:center;width:100%;height:100%}

